.logo {
  object-fit: contain;
  position: fixed;
  left: 20px;
  width: 90px;
}
.avatar {
  width: 30px;
  object-fit: contain;
  position: fixed;
  right: 20px;
}
.navbar {
  display: flex;
  position: fixed;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 30px;
  z-index: 1;
  justify-content: space-between;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.navbar_black {
  background-color: black;
}
